	module.exports = {"data":{"markdownRemark":{"headings":[{"value":"Background","depth":2},{"value":"Target Audience","depth":2},{"value":"The Brand","depth":2}],"html":"<h2>Background</h2>\n<p>The product is a brand guideline book for the fictional plant-sitting service Bloomsitter. The objective of this project was to create a visual identity that is friendly, versatile, and professional, allowing for plenty of colourful expression. While there are plenty of house sitting and pet sitting services, there are no services that care for the plants in a similar manner, which either leaves the plants in the untrained hands of friend &#x26; family or in no hands at all. Bloomsitter is the first ever service that looks after the plants of people who are travelling for extended periods of time, providing the care and nourishment they need to survive.</p>\n<p>![](../assets/intro spread copy.jpg)</p>\n<h2>Target Audience</h2>\n<p>The target audience for these services is composed of two groups: young adults who will be away from home, and elderly people who need an extra helping hand in taking care of their plants. Plant care and travelling are quite popular among younger people today while gardening is a well-known hobby among the elderly, making both groups good targets. </p>\n<p>![](../assets/logo spread copy.jpg)</p>\n<h2>The Brand</h2>\n<p>Bloomsitter has three colour schemes to work with, making its image more versatile. The main aspects of the branding include flowery imagery and gradients, which work together to create a fresh and vibrant display, akin to flowers themselves. This approach lends itself well to the goal of making the brand identity both friendly and professional. The three sections of the brand book, About Us, Visuals and Applications, are divided by the colour schemes.</p>\n<p>![](../assets/stationary spread copy.jpg)</p>","frontmatter":{"title":"Bloomsitter Brand","graduate":"Martina Serafimovska","category":"Brand Identity","category2":"Book Design","category3":null,"image":{"childImageSharp":{"sizes":{"base64":"data:image/jpeg;base64,","aspectRatio":1.5,"src":"/the-next-website/static/bloomsitter 01-020afbe9ddcb04f9109229da52d267d6-75e94.jpg","srcSet":"/the-next-website/static/bloomsitter 01-020afbe9ddcb04f9109229da52d267d6-7516d.jpg 375w,\n/the-next-website/static/bloomsitter 01-020afbe9ddcb04f9109229da52d267d6-b4e9d.jpg 750w,\n/the-next-website/static/bloomsitter 01-020afbe9ddcb04f9109229da52d267d6-75e94.jpg 1000w","sizes":"(max-width: 1000px) 100vw, 1000px"}}}}}},"pathContext":{"slug":"/work/bloomsitter-brand/","name":"Bloomsitter Brand"}}

